@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 100;
    border-bottom: 2px solid #FFDFAF;
    background-color: #E4D7FF;
    height:70px;
}

.header_logo {
    object-fit: contain;
    height: 30px;
    color: #4C269A;
    font-family: 'Pacifico', cursive;
    font-size: 20px;
    padding-top: 5px;
}

.item1_image {
 width: 120px;
 height: 120px;
 border-radius: 20px
}

.item1_header {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.item1_header_text{
    padding-left: 20px;
    margin-left: 5px;
    font-family: "Montserrat", serif;
}

.description, .descriptiontext {
    margin: 0;
}

a {
    text-decoration: none;
    color: #191C32;
}

.memberItem{
    margin-top: 10px;
    width: 100%;
}

.interestImg {
    width: 30px;
    height: 30px;
    margin: 5px 5px;
}

.itemInterests {
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}
.memberContainer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    list-style: none;
    padding: 0;

}
.memberBlock {
    display: flex;
    justify-content: center;
    flex-direction: row;
    background-color: #E4D7FF;
    width: 600px;
    padding: 10px;
    max-width: 85vw;
    height: 30vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    margin-top: 2vh;
    border: 2px solid #4C269A;

}

.btn{
    border: 1px solid black;
    margin-top: 5px;
    width: 90%; 
    overflow: scroll;
    background-color:#4C269A;
    color: white;
    font-weight: 800;
    font-family: "Montserrat", serif;
}

.dropdown{
    display:flex; 
    justify-content: center;
    align-items: center;
}

.dropdown-menu.show{
    width: 90%;
    max-height: 15.5em;
    overflow-y: auto;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(76,38,154,.25)

}
.btn:hover{
    color:white;
}
.form-check-input{
    border: 1px solid rgba(76,38,154,.25)
}
.dropdown-item{
    color: #191C32;
}
.footer {
    position: fixed; 
    bottom: 0;
    display: flex; 
    width: 100%; 
    justify-content: space-evenly;
    background-color: #E4D7FF;
    padding: 5px;
    border-top: 2px solid #FFDFAF;


}

.footer .MuiIconButton-root {
    color: #4C269A
}
.detailContainer {
    display: flex;
    flex-direction: column;
    background-color: white;
    justify-content: space-between;
    width: 600px;
    max-width: 85vw;
    padding: 20px;
    height: 67vh;
    margin-top: 15px;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    border: 1px solid #4C269A;
    color: #191C32;
    background-color: #E4D7FF;

}

.memberDetail{
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
}

.interests{
    display: flex;
    justify-content: space-between;
}

.interest_text{
    font-size: 14px;
}

.chatButton{
    border: 1px solid black;
    width: 90%; 
    background-color:#4C269A;
    color: white;
    font-weight: 800;
    font-size: 1.2rem;
    font-family: "Montserrat", serif;
    border-radius: .25rem;
    cursor: pointer;
    line-height: 1.5;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    width: 600px;
    max-width: 85vw;
    padding: 5px 0;
    margin-top: 7px;
}


.container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-family: "Montserrat", serif;
    height: 100vh ;
    padding: 20px;
}

.card {
    margin-bottom: 20px;
    border: none;
}

.box {
    width: 100%;
    height: 100%;
    padding: 10px;
    position: absolute;
    text-align: center;
    transition: 0.25s;
}

.box input[type="text"],
.box input[type="password"] {
    border: 0;
    background: none;
    display: block;
    margin: 10px auto;
    text-align: center;
    border: 2px solid #4C269A;
    padding: 5px 5px;
    width: 200px;
    outline: none;
    color: black;
    border-radius: 24px;
    transition: 0.25s
}

.box h1 {
    color: #191C32;
    text-transform: uppercase;
    font-weight: 500
}

.box input[type="text"]:focus,
.box input[type="password"]:focus {
    width: 250px;
    border-color: #4C269A;
}

.box input[type="submit"] {
    border: 0;
    background: none;
    display: block;
    margin: 10px auto;
    text-align: center;
    border: 2px solid #4C269A;
    padding: 10px 40px;
    outline: none;
    color: #191C32;
    border-radius: 20px;
    transition: 0.25s;
    cursor: pointer
}

.box input[type="submit"]:hover {
    background: #4C269A;
    color: white;
    font-weight: 700;
}

.forgot {
    text-decoration: underline;
}

.registerImage{
    height:50%;
    width: 50%;
}

.register_container{
    height: 70vh;
}

.registerView{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

input[type="file"] {
    display: none;
}

.custom-file-upload {
    border: 0;
    background: none;
    display: block;
    margin: 10px auto;
    text-align: center;
    border: 2px solid #4C269A;
    padding: 5px 5px;
    width: 200px;
    outline: none;
    color: #191C32;
    border-radius: 24px;
    transition: 0.25s;
    cursor: pointer;

}
.conversation_row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    height: 70px;
    border-bottom: 1px solid #fafafa;
    cursor:pointer;
    margin-top: 10px;
}

.conversation_row:hover{
    background-color: rgb(228, 215, 255);
}

.conversation_img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}

.conversation_name{
    font-weight: 700;
    font-family: "Montserrat", serif;
}

.conversation_details {
    flex: 1 1;
}

.conversation_details > p{
    color: gray;
}
.message{
    display: flex;
    flex-direction: column;
    margin-top: 5px;
}

.messageImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.messageTop{
    display: flex;
    align-items: center;
}

.messageText{
    padding: 10px;
    border-radius: 20px;
    background-color: #4C269A;
    color: white;
    max-width: 250px;
    margin-bottom: 5px;
}

.messageBottom{
    font-size: 12px;
    margin-bottom: 10px;
    color: grey
}

.message.own{
    align-items: flex-end;
}

.message.own .messageText{
    background-color: #FFDFAF;
    color: #191C32;
}
.chatBox{
    height: calc(100vh - 150px);
}

.chatBoxBottom{
    display: flex;
    padding: 5px;
    position: fixed;
    bottom: 0;
    width: 100%;
    border-top: 1px solid lightgray;

}

.chatMessageInput{
    width: 90%; 
    height: 70px;
    padding: 10px;
    flex: 1 1;
    border: none; 
}

.chatSubmitButton{
border: none;
margin-right: 20px;
background-color: white;
font-weight: bolder;
color: #F1884E;
cursor: pointer;
}

.chatBoxWrapper{
    padding: 10px;
    height: 100%;
    display: flex; 
    flex-direction: column;
    position: relative;
}

.chatBoxTop{
    height: 100%;
    overflow-y: scroll;
    padding-right: 10px;
}

.noConversationText{
    position: absolute; 
    top: 10%;
    font-size: 20px; 
    color: rgb(190, 189, 189); 
    cursor: default;
}
