@import url("https://fonts.googleapis.com/css2?family=Montserrat&family=Pacifico&display=swap");

.item1_image {
 width: 120px;
 height: 120px;
 border-radius: 20px
}

.item1_header {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.item1_header_text{
    padding-left: 20px;
    margin-left: 5px;
    font-family: "Montserrat", serif;
}

.description, .descriptiontext {
    margin: 0;
}

a {
    text-decoration: none;
    color: #191C32;
}

.memberItem{
    margin-top: 10px;
    width: 100%;
}

.interestImg {
    width: 30px;
    height: 30px;
    margin: 5px 5px;
}

.itemInterests {
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}